import React from 'react'

import { BigNumber } from '@ethersproject/bignumber'
import { formatEther } from '@ethersproject/units'

function Number(props: {
  number: BigNumber
  ticker?: string
  showFirstDigitsNumber?: number
}) {
  let value = formatEther(props.number)
  if (!!props.showFirstDigitsNumber) { value = value.slice(0, props.showFirstDigitsNumber!) }

  if (!!props.ticker) {
    return <>{`${props.ticker} ${value}`}</>
  }

  return <>
    <span style={{fontFamily: 'Roboto'}}>Îž</span>
    &nbsp;
    {value}
  </>
}

function AddressLink(props: {
  address: string
  chainId: 1 | 4 | 42
}) {
  const url = `https://${ props.chainId === 42 ? `kovan.` : (props.chainId === 4 ? `rinkeby.` : ``)}etherscan.io/address/${props.address}`
  return <a href={url} target="_blank">{`${props.address.slice(0, 10)}...`}</a>
}

export default { Number, AddressLink }